import Vue from 'vue';
import { Col, Row, Button } from 'element-ui';
import Result from 'components/Result';
Vue.use(Button);
Vue.use(Col);
Vue.use(Row);
Vue.use(Result);
export default Vue.extend({
    data() {
        return {};
    },
    computed: {
        formData() {
            return this.$store.state.form.step;
        }
    },
    methods: {
        onFinish() {
            this.$router.push('/form/step-form');
        }
    }
});



